.sketch-container {
    background-color: #5ADF8E;
    padding:1rem 5rem 5rem 5rem;
}
.sketch{
    font-size: 1.4rem;
    font-weight: 700;
    letter-spacing: .1rem;
    margin-bottom: 3rem;
}

.sketch-container .bodyText {
    letter-spacing: .1rem;
    line-height:1.5rem;
    margin-bottom:3rem;
}

.screenshots {
    display:flex;
}

.screenshots .sketchShot {
    flex-flow: row;
    margin-bottom:3rem;
}

.sketchShot .column {
    flex: 50%;
}

.screenshots .sketchShot img {
    border-radius: 5rem;
    padding-right:2rem;
    height:60vh;
    width:44.5vw;
}

.scaleContainer {
    letter-spacing: .1rem;
    text-align: center;
    width:100%;
}

p {
    padding-top:4%
}

.scaleContainer .inner50 {
    border-radius: 3rem;
    min-width:42.8%;
    padding: 3%;
    display:inline-block;
    text-align: center;
    border:2px solid black;
}

.inner50.right {
    margin-left:1%;
    height:100%;
}

.inner50.left {
    margin-right:1%;
}

.inner50 img {
    max-width: 100%;
    max-height:100%;
    border-radius: 3rem;
}

@media only screen and (max-width: 2255px) {
    .inner50.right {
        margin-left:0%;
    }
    .inner50.left {
        margin-right:0%;
    }
    .inner50 {
        width:94%;
    }
}

@media only screen and (max-width: 600px) {
    .sketch {
        padding:2rem;
    }
}